import React from 'react';
import Scrollspy from 'react-scrollspy-ez';

class ScrollspyContainer extends React.Component {
    render() {
        return (
            <Scrollspy 
                ids={this.props.ids}
                itemContainerClassName="nav nav-pills flex-column mb-auto"
                itemClassName="nav-link"
                activeItemClassName="active"
                includeParentClasses={true}>                                  
            </Scrollspy>
        )
    }
}

export default ScrollspyContainer;