import React from 'react';
import { Route, Switch, Link } from "react-router-dom";


import background from '../img/home.jpg';
import robot from '../img/robot.jpg';
import pickle from '../img/pickle.jpg';
import linux from '../img/linux.png';
import arcade from '../img/arcade.jpg';
import cyberpunk from '../img/cyberpunk.png';
import internal from '../img/internal.png';
import jurassic from '../img/jurassic.png';
import dave from '../img/dave.png';

class CTF extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            boxes: [
                {
                    id: 1,
                    name: 'PickleRick',
                    platform: "TryHackMe.com",
                    difficulty: 'Easy',
                    diffClass: "card-easy",
                    categories: ['Linux', 'RCE'],
                    img: pickle,
                    show: true,
                    route: "/picklerick",


                },
                {
                    id: 2,
                    name: 'RootMe',
                    platform: "TryHackMe.com",
                    difficulty: 'Easy',
                    diffClass: "card-easy",
                    categories: ['Linux','Misconfiguration'],
                    img: linux,
                    show: true,
                    route: "/rootme",


                },
                {
                    id: 3,
                    name: 'Mr Robot',
                    platform: "TryHackMe.com",
                    difficulty: 'Medium',
                    diffClass: "card-medium",
                    categories: ['Linux', 'Wordpress', 'Misconfiguration','Brute'],
                    img: robot,
                    show: true,
                    route: "/mrrobot",

                },
                
                {
                    id: 4,
                    name: 'Retro',
                    platform: "TryHackMe.com",
                    difficulty: 'Hard',
                    diffClass: "card-hard",
                    categories: ['Windows', 'Wordpress','CVE'],
                    img: arcade,
                    show: true,
                    route: "/retro",


                },
                {
                    id: 5,
                    name: 'DifferentCTF',
                    platform: "TryHackMe.com",
                    difficulty: 'Hard',
                    diffClass: "card-hard",
                    categories: ['Linux', 'Wordpress','Stego','Reverse'],
                    img: cyberpunk,
                    show: true,
                    route: "/differentctf",


                },
                {
                    id: 6,
                    name: 'Internal',
                    platform: "TryHackMe.com",
                    difficulty: 'Hard',
                    diffClass: "card-hard",
                    categories: ['Linux', 'Wordpress','Docker','Brute'],
                    img: internal,
                    show: true,
                    route: "/internal",


                },
                {
                    id: 7,
                    name: 'Jurassic Park',
                    platform: "TryHackMe.com",
                    difficulty: 'Hard',
                    diffClass: "card-hard",
                    categories: ['Linux', 'SQLi','Database'],
                    img: jurassic,
                    show: true,
                    route: "/jurassic",


                },
                {
                    id: 8,
                    name: 'Dave\'s Blog',
                    platform: "TryHackMe.com",
                    difficulty: 'Hard',
                    diffClass: "card-hard",
                    categories: ['Linux','Database','Reverse','BinExp'],
                    img: dave,
                    show: true,
                    route: "/davesblog",


                },
            ],
            icons: [
                {
                    id: 1,
                    name: 'Wordpress',
                    link: 'fab fa-wordpress-simple',
                    checked: false,
                },
                {
                    id: 2,
                    name: 'Linux',
                    link: 'fab fa-linux',
                    checked: false,

                },
                {
                    id: 3,
                    name: 'CVE',
                    link: 'fas fa-bug',
                    checked: false,

                },
                {
                    id: 4,
                    name: 'SQLi',
                    link: 'fas fa-syringe',
                    checked: false,

                },
                {
                    id: 5,
                    name: 'LFI',
                    link: 'fas fa-binoculars',
                    checked: false,

                },
                {
                    id: 6,
                    name: 'Misconfiguration',
                    link: 'fas fa-exclamation',
                    checked: false,

                },
                {
                    id: 7,
                    name: 'Sandbox Escape',
                    link: 'fas fa-lock',
                    checked: false,

                },
                {
                    id: 8,
                    name: 'RCE',
                    link: 'fas fa-search',
                    checked: false,

                },
                {
                    id: 9,
                    name: 'Windows',
                    link: 'fab fa-windows',
                    checked: false,

                },
                {
                    id: 10,
                    name: 'Stego',
                    link: 'fas fa-paw',
                    checked: false,

                },
                {
                    id: 11,
                    name: 'Reverse',
                    link: 'fas fa-history',
                    checked: false,

                },
                {
                    id: 12,
                    name: 'Docker',
                    link: 'fas fa-box-open',
                    checked: false,

                },
                {
                    id: 13,
                    name: 'Brute',
                    link: 'fas fa-infinity',
                    checked: false,

                },
                {
                    id: 14,
                    name: 'BinExp',
                    link: 'fab fa-stack-overflow',
                    checked: false,

                },
                {
                    id: 15,
                    name: 'Database',
                    link: 'fas fa-database',
                    checked: false,

                },

                

            ],
            currentFilter: "",
        }

        this.handleSearch = this.handleSearch.bind(this);
        this.handleCheck = this.handleCheck.bind(this);
        this.updateState = this.updateState.bind(this);

    }

    updateState(tempIcons = this.state.icons, tempBoxes = this.state.boxes, filter = "undef") {
        let checkedIcons = [];
        let tempFilter = "";
        if (filter == "undef") {
            tempFilter = this.state.currentFilter;
        } else {
            tempFilter = filter;
        }
        
        
        tempIcons.forEach(icon => {
            if (icon.checked) {
                checkedIcons.push(icon.name);
            } 
        })


        
        tempBoxes.forEach((box, index) => {
            if (checkedIcons.length == 0 && tempFilter.length == 0) {
                tempBoxes[index].show = true;
            } else if (checkedIcons.length == 0 && tempFilter.length > 0){
                if (box.name.includes(tempFilter)) {
                    tempBoxes[index].show = true;
                } else {
                    tempBoxes[index].show = false;
                }
            }
            checkedIcons.forEach(icon => {
                if (!box.categories.includes(icon) && box.show == true) {
                    tempBoxes[index].show = false;
                } else if (box.categories.includes(icon) && box.show == false) {
                    tempBoxes[index].show = true;
                }
            })
        })
        

        if (filter == "undef") {
            this.setState({icons: tempIcons, boxes: tempBoxes});
        } else {
            this.setState({icons: tempIcons, boxes: tempBoxes, currentFilter: tempFilter});
        }
        
    }

    handleSearch(event) {
        let val = event.target.value;
        let tempBoxes = this.state.boxes;
        tempBoxes.forEach((box, index) => {            
            if (box.name.includes(val)) {
                tempBoxes[index].show = true;
            } else {
                tempBoxes[index].show = false;
            }
        });
        
        this.updateState(undefined, tempBoxes, val);
    }

    handleCheck(event) {
        let val = event.target.id;
        let tempIcons = this.state.icons;
        if (event.target.checked) {
            tempIcons.forEach((icon, index) => {
                if (icon.name == val) {
                    
                    tempIcons[index].checked = true;
                    
                } 
            })
        } else {
            tempIcons.forEach((icon, index) => {
                if (icon.name == val) {
                    tempIcons[index].checked = false;
                } 
            })
        }
        this.updateState(tempIcons, undefined, undefined);
        
    }
    
    render() {
        return (
            <div>
                <div className="jumbotron">
                    <div className="input-group mb-3">
                        <span className="input-group-text" id="basic-addon1"><i className="fas fa-search"></i></span>
                        <input type="text" className="form-control" onChange={this.handleSearch} placeholder="Search..." aria-label="Search..." aria-describedby="basic-addon1"></input>
                    </div>
                    {this.state.icons.map(icon => {
                        return (
                            <div className="form-check">
                                
                                <input className="form-check-input" onChange={this.handleCheck} type="checkbox" value="" id={icon.name}></input>
                                <label className="form-check-label" for={icon.name}>
                                    {icon.name} 
                                    <i className={icon.link}></i>
                                </label>
                                
                            </div>
                        )
                    })}
                    
                    
                </div>
                <div className="carousel landing">
                    <div className="carousel-inner">
                        <div className="carousel-item active">
                            <img id="backg" src={background} alt="Background image"></img>
                            
                            <div className="carousel-caption">
                                <div className="row">
                                    {this.state.boxes.map(box => {
                                        if (box.show == false) {
                                            return false;
                                        }
                                        return (
                                            <div className="col-sm-3" >
                                                <div className="card">
                                                    <img src={box.img} className="card-img-top" alt="test-image"></img>
                                                    <div className="card-body">
                                                        <p className={box.diffClass}>{box.difficulty}</p>
                                                        <h5 className="card-title">{box.name}</h5>

                                                        <Link to={box.route} className="btn btn-outline-secondary btn-sm">View</Link>
                                                        <div className="card-footer">
                                                        {box.categories.map(category => {
                                                            return (
                                                                <div className="card-category">
                                                                    {this.state.icons.map(icon => {
                                                                        if (icon.name === category) {
                                                                            return (
                                                                                <i className={icon.link}></i>
                                                                            )
                                                                        } 
                                                                        
                                                                    })}
                                                                    
                                                                    <p>{category}</p>
                                                                </div>
                                                            )
                                                        })}
                                                        </div>
                                                    
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })}

                                    
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
        )
    }
}



export default CTF;