import React from 'react';

class ImageCap extends React.Component {
    render() {
        return (
            <figure className="figure">
                <img src={this.props.image} className="figure-img img-fluid rounded" alt={this.props.text}></img>
                <figcaption className="figure-caption">{this.props.text}</figcaption>
            </figure>
        )
    }
}

export default ImageCap;