import React from 'react';
import background from '../img/home.jpg';

import landing from '../img/retro/landing.png';

{/*
    Sidenav:
        Hacking
            Resources
            OWASP Top 10
            Networking
            Tools
                Password Cracking
                Scanning
                etc...
        Programming
            Ada
            Python
            C#
            C++



*/}

class Dictionary extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            navIcons: [
                {
                    id: 1,
                    name: "Hacking",
                    collapseID: "hackingCollapse",
                    classes: "fas fa-greater-than",
                    expand: false,
                    subHeading: [
                        "Resources",
                        "Tools",
                    ]
                },

            ]

            
        }


        this.handleToggle = this.handleToggle.bind(this);
    }
    
    handleToggle(name) {
        let tempNavs = this.state.navIcons;
        let thisID = name;

        tempNavs.forEach(nav => {
            if (thisID == nav.name) {
                if (nav.expand) {
                    nav.classes = "fas fa-greater-than rotate-anticlockwise";
                    nav.expand = false;
                } else {
                    nav.classes = "fas fa-greater-than rotate-clockwise";
                    nav.expand = true;
                }
                
            }
        })

        this.setState({navIcons: tempNavs});
    }

    render() {
        return (
            <div className="writeup">
                <div className="container-fluid">
                    <div className="row bg-dark">
                        <nav className="col-3 px-1 position-fixed offset">
                            <ul className="nav nav-pills flex-column mb-auto">
                                <li className="nav-link">
                                    Index
                                </li>
                                {this.state.navIcons.map(icon => {
                                    return (
                                        <li className="nav-link" onClick={() => this.handleToggle(icon.name)} >
                                            <a className="nav-link" role="button" data-bs-toggle="collapse" data-bs-target={"#" + icon.collapseID} aria-expanded="false" aria-controls={icon.collapseID}>
                                                <i className={icon.classes} id={icon.name}></i>
                                            </a>
                                            <div className="collapse" id={icon.collapseID}>
                                                <ul>
                                                    {icon.subHeading.map(sub => {
                                                        return (
                                                            <li className="nav-link">{sub}</li>
                                                        )
                                                    })}
                                                </ul>
                                            </div>
                                        </li>
                                    )
                                })}
                                
                                <li className="nav-link">
                                    Programming
                                </li>
                                <li className="nav-link">
                                    Cyber Security
                                </li>
                                <li className="nav-link">
                                    Networking
                                </li>
                            </ul>
                        </nav>
                        <div className="content col offset-3">
                            <div className="carousel landing">
                                <div className="carousel-inner">
                                    <div className="carousel-item active landing-container">
                                        <img className="writeup-landing" src={landing} alt="Background image"></img>
                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
        )
    }
}



export default Dictionary;