import React from 'react';
import ScrollspyContainer from '../ScrollspyContainer';
import ImageCap from './Helpers/ImageCap';

import landing from '../../img/linux.png';
import gobuster from '../../img/rootme/gobuster.png';
import homepage from '../../img/rootme/homepage.png';
import ncListen from '../../img/rootme/nc-listen.png';
import nmap from '../../img/rootme/nmap.png';
import notAllowed from '../../img/rootme/not-allowed.png';
import rootPerm from '../../img/rootme/root-1.png';
import rootFlag from '../../img/rootme/root-flag.png';
import revShell from '../../img/rootme/shell-caught.png';
import uploadSuc from '../../img/rootme/success-upload.png';
import suid from '../../img/rootme/suid.png';
import uploadDir from '../../img/rootme/uploads.png';
import uploadSource from '../../img/rootme/upload-source-code.png';
import userFlag from '../../img/rootme/user-flag.png';


class RootMe extends React.Component {
    render() {
        return (
            <div className="writeup">
                <div className="container-fluid" >
                    <div className="row bg-dark ">
                        <nav className="col-3 px-1 position-fixed offset" >
                            <a href="https://tryhackme.com/room/rrootme" target="_blank" className="nav-header">RootMe</a>
                            <hr></hr>
                            <ScrollspyContainer ids={['render','intro','enumeration','portscan','dirdis','exploitation','uploadvuln','privesc','rootuser','userflag','rootflag']}></ScrollspyContainer>   
                            <hr></hr>                     
                        </nav>


                        <div className="content col offset-3" >
                            <div id="render" className="render"></div>
                            <div>
                                <div className="carousel landing">
                                    <div className="carousel-inner">
                                        <div className="carousel-item active landing-container">
                                            <img className="writeup-landing" src={landing} alt="Background image"></img>
                                            
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <h1 id="intro">Introduction</h1>
                            <p>This is a beginner-friendly box that features common vulnerabities in the form of file uploads and SUID files.</p>
                            <p>RootMe falls into the following categories:</p>
                            <ul>
                                <li>Linux</li>
                                <li>Unrestricted File Upload</li>
                                <li>Misconfiguration</li>
                            </ul>
                            <hr></hr>
                            <h1 id="enumeration">Enumeration</h1>
                            <h3 id="portscan" className="level-2">Port Scanning</h3>
                            <p>First, I used nmap to scan the box for open ports.</p>
                            <p className="code-snippet">nmap -sS -sC -sV -O 10.10.77.100 -oN nmap.scan</p>
                            <p>The above command explained:</p>
                            <ul>
                                <li><span className="code-snippet inline">-sS</span> -  Uses a TCP SYN scan, this type of scan never fully forms a connection with the target. Instead, a specially crafted packet is sent and the response is analyzed to produce scan results.</li>
                                <li><span className="code-snippet inline">-sC -sV -O</span> - These tell nmap to use common scripts, attempt to detect the version of running services, and attempt to detect the operating system.</li>
                                <li><span className="code-snippet inline">-oN</span> - This saves the output of the scan to the target file.</li>
                            </ul>
                            <ImageCap image={nmap} text="nmap scan results." />
                            
                            <p>The scan revealed an Apache site running on port 80, I then used Firefox to investigate further.</p>
                            <ImageCap image={homepage} text="homepage of the website." />
                            
                            <hr></hr>
                            <h3 id="dirdis" className="level-2">Directory Discovery</h3>
                            <p>The homepage and source code offered no information, the next step was to use gobuster to find any hidden directories.</p>
                            <p className="code-snippet">gobuster dir -w /usr/share/wordlists/dirbuster/directory-list-2.3-medium.txt  -u 10.10.77.100 -o root-gobuster.scan</p>
                            <p>Straight away the gobuster scan revealed some interesting directories:</p>
                            <ImageCap image={gobuster} text="gobuster scan results." />
                            
                            <hr></hr>
                            <h1 id="exploitation">Exploitation</h1>
                            <h3 id="uploadvuln" className="level-2">Upload Vulnerability</h3>
                            <p>Navigating to <span className="code-snippet inline">/panel</span> allows me to upload a file to the server. This has potential for a file upload vulnerability.</p>
                            <p>A quick look at the source code shows that there is no client-side error-checking for my file upload which means any security checks will happen server-side.</p>
                            <ImageCap image={uploadSource} text="upload page source code." />
                            
                            <p>First, I tried uploading an empty test.php file to see if it was blocked:</p>
                            <ImageCap image={notAllowed} text="upload failure." />
                            
                            <p>This did not work. This time, I tried a more obscure php filetype, <span className="code-snippet inline">php5</span>. If this website is using a blacklist, then a more obscure filetype might make it through the security checks.</p>
                            <p>Again, I uploaded an empty test.php5.</p>
                            <ImageCap image={uploadSuc} text="upload success." />
                            
                            <p>Success! Now I can navigate to <span className="code-snippet inline">/uploads</span> and find my uploaded file. This will be how I execute a reverse shell.</p>
                            <p>Next, I uploaded a php reverse shell, <span className="code-snippet inline">shell.php5</span>.</p>
                            <ImageCap image={uploadDir} text="index of uploads." />
                            
                            <p>Before executing my shell, I setup a nc listener on my machine:</p>
                            <p className="code-snippet">&gt; nc -lvnp 4444</p>
                            <br></br>
                            <ImageCap image={ncListen} text="my nc listener." />
                            
                            <p>Upon execution I successfully caught a reverse shell.</p>
                            <ImageCap image={revShell} text="reverse shell caught." />
                           
                            <hr></hr>
                            <h1 id="privesc">Privilege Escalation</h1>
                            <h3 id="rootuser" className="level-2">Root User</h3>
                            <h5 id="userflag" className="level-3">User Flag</h5>
                            <p>The user flag was found in <span className="code-snippet inline">/var/www</span>:</p>
                            <ImageCap image={userFlag} text="user flag found." />
                            
                            <p>Now I needed to escalate my privileges and become root user.</p>
                            <p>I used the following command to find all files with the SUID bit set:</p>
                            <p className="code-snippet">find / -perm /4000 2&gt;/dev/null</p>
                            <p>The above command explained:</p>
                            <ul>
                                <li><span className="code-snippet inline">find /</span> - Search for files, starting at the root directory.</li>
                                <li><span className="code-snippet inline">-perm /4000</span> - Only return files that have the SUID bit set.</li>
                                <li><span className="code-snippet inline">2&gt;/dev/null</span> - Filters out the errors, '2' represents the error descriptor, and '/dev/null' is where output is sent to be ignored.</li>
                            </ul>
                            <ImageCap image={suid} text="results from find command." />
                            
                            <p>There are many results, but one file that stands out is python.</p>
                            <p>The following command can be used to spawn a root shell when python has the SUID bit set:</p>
                            <p className="code-snippet">python -c 'import os; os.execl("/bin/sh", "sh", "-p")'</p>
                            <br></br>
                            <ImageCap image={rootPerm} text="acquiring root perms." />
                            
                            <hr></hr>
                            <h5 id="rootflag" className="level-3">Root Flag</h5>
                            <p>Now I can find the root flag in <span className="code-snippet inline">/root</span>:</p>
                            <ImageCap image={rootFlag} text="the final root flag." />
                            
                        

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default RootMe;