import React from 'react';

import ScrollspyContainer from '../ScrollspyContainer';
import ImageCap from './Helpers/ImageCap';

import landing from '../../img/jurassic.png';
import dirsearch from '../../img/jurassic/dirsearch.png';
import rootCreds from '../../img/jurassic/first-root.png';
import flagOne from '../../img/jurassic/flag1.png';
import flagTwo from '../../img/jurassic/flag2.png';
import flagThree from '../../img/jurassic/flag3.png';
import flagFive from '../../img/jurassic/flag5.png';
import flagTwoHint from '../../img/jurassic/flag2-hint.png';
import idFive from '../../img/jurassic/id5.png';
import newP from '../../img/jurassic/new-ong.png';
import nmap from '../../img/jurassic/nmap.png';
import sqliFail from '../../img/jurassic/sqli-fail.png';
import sshLogin from '../../img/jurassic/ssh-login.png';
import sudoL from '../../img/jurassic/sudo-l.png';
import unionColumns from '../../img/jurassic/union-columns.png';
import unionDB from '../../img/jurassic/union-db.png';
import unionPW from '../../img/jurassic/union-pw.png';
import unionStart from '../../img/jurassic/union-start.png';
import unionVersion from '../../img/jurassic/union-version.png';

class Jurassic extends React.Component {
    render() {
        return (
            <div className="writeup">
                <div className="container-fluid">
                    <div className="row bg-dark">
                        <nav className="col-3 px-1 position-fixed offset" > 
                            <a href="https://tryhackme.com/room/jurassicpark" target="_blank" className="nav-header">Jurassic Park</a>
                            <hr />
                            <ScrollspyContainer ids={['render','intro','enumeration','portscan','webinv','exploitation','sqli','privesc','vertical','flag1','flag5','flag2','flag3']} />
                            <hr />
                        </nav>
                        <div className="content col offset-3">
                            <div className="render" id="render"></div>
                            <div className="carousel landing">
                                <div className="carousel-inner">
                                    <div className="carousel-item active landing-container">
                                    <img className="writeup-landing" src={landing} alt="Background image"></img>
                                    
                                    </div>
                                </div>
                            </div>

                            <h1 id="intro">Introduction</h1>
                            <p>Jurassic Park is a medium/hard box featuring SQL injection and some PrivEsc in order to obtain 4 flags.</p>
                            <p>This box only falls into the following categories:</p>
                            <ul>
                                <li>- Linux</li>
                                <li>- SQL Injection</li>
                            </ul>
                            <hr />
                            <h1 id="enumeration">Enumeration</h1>
                            <h3 id="portscan" className="level-2">Port Scanning</h3>
                            <p>First, I used nmap to scan the box for any open ports:</p>
                            <p className="code-snippet">&gt; nmap -sS -sC -sV 10.10.70.49 -oN nmap.scan</p>
                            <br />
                            <ImageCap image={nmap} text="results from nmap scan." />
                            <hr />
                            <h3 id="webinv" className="level-2">Website Investigation</h3>
                            <p>I then used dirsearch to find hidden directories and files on the webserver:</p>
                            <p className="code-snippet">&gt; /opt/dirsearch/dirsearch.py -u 10.10.70.49 -e php,txt -f -w /usr/share/wordlists/dirbuster/directory-list-2.3-medium.txt</p>
                            <br />
                            <ImageCap image={dirsearch} text="results from dirsearch." />
                            <p>A brief look into shop.php shows packages to buy. When one of the packages is selected, we are redirected to item.php with the GET parameter 'id'.</p>
                            <hr />
                            <h1 id="exploitation">Exploitation</h1>
                            <h3 className="level-2" id="sqli">SQL Injection</h3>
                            <p>I first tried adding an apostraphe to confirm SQL injection was a viable attack vector:</p>
                            <ImageCap image={sqliFail} text="results from apostraphe." />
                            <p>With this failing, I tried inputting different values for 'id' and found this:</p>
                            <ImageCap image={idFive} text="hint found." />
                            <p>This explains the error found earlier and limits what types of SQL injection I can use. However, with these limitations, UNION injection can still be exploited.</p>
                            <p>In SQL, UNION allows another query to be executed and returns results from both queries. This can be used to detect the number of columns in the database. </p>
                            <p>For a UNION statement to work, the results from both queries must have the same number of columns. This means that if I use UNION with the id GET parameter, I can return multiple NULL values until no error is received. Then, however many NULL values are returned is the number of columns in the table queried by id.</p>
                            <p>For example, using one NULL value:</p>
                            <ImageCap image={unionStart} text="result from using one NULL value." />
                            <p>This error indicates that the amount of NULL values used does not match the number of columns in the table.</p>
                            <p>So I kept incrementing the amount of NULL's until no error was returned:</p>
                            <p className="code-snippet">http://10.10.70.49/item.php?id=5 union select NULL,NULL,NULL,NULL,NULL</p>
                            <br />
                            <ImageCap image={unionColumns} text="correct number of NULL's found." />
                            <p>Five ended up being the correct number of columns:</p>
                            <p>Next, I replaced each NULL value with a number to ascertain if this would return an error.</p>
                            <p >This is done because I will need to view database information on the webpage.</p>
                            <p className="code-snippet">http://10.10.70.49/item.php?id=5 union select 1,2,3,4,5</p>
                            <br />
                            <ImageCap image={newP} text="result from the above query." />
                            <p>Now I know the second column is displayed as the title, I start using this column to get information that will answer the questions.</p>
                            <p className="code-snippet">http://10.10.70.49/item.php?id=5 union select 1,database(),3,4,5</p>
                            <br />
                            <ImageCap image={unionDB} text="database name found through injection." />
                            <p className="code-snippet">http://10.10.70.49/item.php?id=5 union select 1,version(),3,4,5</p>
                            <br />
                            <ImageCap image={unionVersion} text="system version found through injection." />
                            <p className="code-snippet">http://10.10.70.49/item.php?id=5 union select 1,password,3,4,5 from users</p>
                            <br />
                            <ImageCap image={unionPW} text="Dennis' passsword found through injection." />
                            <p>Note: the table name 'users' and column name 'password' were guesses that resulted from trial and error to find the right values.</p>
                            <p>With a username and password, I used SSH to login to the server:</p>
                            <ImageCap image={sshLogin} text="ssh login to the server." />
                            <hr />
                            <h1 id="privesc">Privilege Escelation</h1>
                            <h3 id="vertical" className="level-2">Vertical Escalation</h3>

                            <h5 id="flag1" className="level-3">Flag One</h5>
                            <p>The first flag is found in Dennis home directory:</p>
                            <ImageCap image={flagOne} text="flag one found." />

                            <p>To escalate my privileges, I first ran <span className="code-snippet inline">sudo -l</span>:</p>
                            <ImageCap image={sudoL} text="results from sudo -l." />
                            <p>As it turns out, Dennis can run scp with sudo, this can be used to gain root privileges:</p>
                            <ImageCap image={rootCreds} text="gaining root from scp exploit" />
                            <hr />
                            <h5 id="flag5" className="level-3">Flag Five</h5>
                            <p>And flag five is found:</p>
                            <ImageCap image={flagFive} text="flag five is found." />
                            <p>To give myself a more stable shell, I added my public SSH key into roots authorized keys. And then used SSH to login as root.</p>
                            <p>From here, I originally thought that we may be in some sort of container, but I could not find any evidence of that. I then thought that maybe I just missed the other two flags, so I started checking files I previously ignored.</p>
                            <hr />
                            <h5 id="flag2" className="level-3">Flag Two</h5>
                            <p>The second flag's location was found in <span className="code-snippet inline">/home/dennis/.viminfo</span>:</p>
                            <ImageCap image={flagTwoHint} text="location of flag two is found." />
                            <p>The contents of flag two:</p>
                            <ImageCap image={flagTwo} text="flag two is found." />
                            <hr />
                            <h5 id="flag3" className="level-3">Flag Three</h5>
                            <p>The third flag was found in <span className="code-snippet inline">/home/dennis/.bash_history</span>:</p>
                            <ImageCap image={flagThree} text="flag three is found." />
                            
                            <p>Despite collecting the flags in the wrong order, all of them have been found.</p>









                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Jurassic;